import React, { Component } from "react";

class FormMutasiCiok extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenis: "",
      bruto: null,
      kadar: null,
      netto: null,
    };
  }
  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  submit = () => {
    console.log(JSON.stringify(this.state));
  };
  render() {
    return (
      <div className="row">
        <div className="col-md-3 form-group">
          <label>Jenis</label>
          <select
            type="text"
            id="jenis"
            name="jenis"
            className="form-control"
            onChange={this.inputChange("jenis")}
          >
            <option value="CK">CK</option>
            <option value="AM">AM</option>
          </select>
        </div>
        <div className="col-md-3 form-group">
          <label>Bruto</label>
          <input
            type="text"
            id="brutto"
            name="brutto"
            className="form-control"
            onChange={this.inputChange("bruto")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label>Kadar</label>
          <input
            type="text"
            id="kadar"
            name="kadar"
            className="form-control"
            onChange={this.inputChange("kadar")}
          />
        </div>
        <div className="col-md-3 form-group">
          <label>Netto</label>
          <input
            type="text"
            id="netto"
            name="netto"
            className="form-control"
            onChange={this.inputChange("netto")}
          />
        </div>
        <div className="col-md-12 col-lg-12">
          <div className="text-right">
            <button className="btn btn-primary" onClick={this.submit}>
              Simpan
            </button>
          </div>
        </div>
      </div>
    );
  }
}

export default FormMutasiCiok;
